          @import "@/assets/core";
        

@import "@/assets/variables";

.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 30px;
  min-height: 300px;
}

.title {
  font-size: 22px;
  margin-bottom: 30px;
  text-align: center;
  max-width: 600px;
}

.qr-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.qr-code {
  width: 250px;
  height: 250px;
  margin-bottom: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  
  // Ensure QR code SVG/Canvas fills container
  :deep(svg), :deep(canvas) {
    width: 100% !important;
    height: 100% !important;
    display: block;
  }
}

.timer {
  font-size: 18px;
  margin-top: 10px;
  color: var(--text-secondary);
}

.loader-container, .message-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 50px;
}

.processing-text {
  margin-top: 20px;
  font-size: 18px;
}

.back-link-container {
  margin-top: 30px;
  text-align: center;
}

.back-link {
  font-weight: 400;
  line-height: normal;
  font-family: inherit;
}

.subtitle-wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: -20px;
  margin-bottom: 30px;
}

.subtitle {
  font-size: 14px;
  color: var(--text-secondary);
  text-align: center;
  max-width: 500px;
  margin: 0 auto;
  
  :deep(a) {
    color: var(--primary);
  }
}

.tv-link {
  margin-top: 8px;
}
